import React from 'react';
import Helmet from 'react-helmet';
import ReactMarkdown from 'react-markdown';

const about = `

#### About the Website




#### About Me

 (Evan Ye) / \`jigsawye\`  
System Developer@Yoctol  
Taipei, Taiwan

##### Skill

Front-end:

- HTML5
- CSS / SASS, PostCSS
- JavaScript, ECMAScript 6/7/8
- Webpack, Babel
- React & React's ecosystem
- Vue & Vue's ecosystem
- Angular @1.x
- Bootstrap, Bulma

Backend:

- PHP, Laravel
- Node.js, Koa, Express
- WebSocket
- MariaDB / MySQL, SQLite
- Redis

Other:

- CI / CD
- Unix Command Line
- Git Version Control

##### Slides

- 
    - [React ](http://slides.com/jigsawye/react-the-gate#/)
    - [ JavaScript](https://docs.google.com/presentation/d/16m7F_Z_AoMiVQcflnOgvpGDmZEI8m1wP1KqpOSfkgdg/edit)
    - [ JavaScript ](https://docs.google.com/presentation/d/1rdYS3Ia_4YOGSqcWmcT0l46ErwjviFgTtjG5xM9_sLw/edit)
    - [JavaScript ](https://docs.google.com/presentation/d/1ckAiWwZOzsdQ4pkqlV1O9DUNdgg-UDJFMa2KKarrM_A/edit#slide=id.gd896f8a3e_0_1)
- Hackathon Taiwan x Taichung
    - [Webpack : Bundle Your Front-end Resources](https://slides.com/jigsawye/webpack-workshop)

##### Contact

- jigsaw.ye@gmail.com
- [@GitHub](https://github.com/jigsawye)
- [@Facebook](https://facebook.com/jigsaw.ye)
- [@LinkedIn](https://www.linkedin.com/in/jigsawye)
`;

const AboutPage = ({ data }) => (
  <div>
    <Helmet title={`ABOUT  ${data.site.siteMetadata.title}`} />
    <ReactMarkdown source={about} />
  </div>
);

export default AboutPage;

export const pageQuery = graphql`
  query AboutQuery {
    site {
      siteMetadata {
        title
      }
    }
  }
`;



